*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-size-adjust: none;
  -webkit-text-size-adjust: none;
  font-family: sans-serif;
}

body{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1b2034;
  color: white;
}
.app{
  padding: 2rem 1rem;
}
h1{
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
input{
  appearance: none;
  outline: none;
  border: none;
  background: #EEE;
  width: 100%;
  max-width: 300px;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
}
button, a{
  appearance: none;
  outline: none;
  border: none;
  background: none;
  color: #2bcb4b;
  font-size: 1.5rem;
  line-height: 1;
  text-decoration: none;
  cursor: pointer;
}

img{
  display: block;
  width: 100%;
  max-width: 480px;
  margin: 2rem auto;
}

